import Swiper from 'swiper/bundle';

/** Composante Carousel de Timtools */
export default class Carousel {
  /**
   * Mthode constructeur
   * @param {HTMLElement} element - lment HTML sur lequel la composante est instancie
   */
  constructor(element) {
    this.element = element;

    // Options par dfaut pour la librairie Swiper
    this.defaultOptions = {
      spaceBetween: 20,
      loop: true,
      breakpoints: {
        // when window width is >= 320px
        320: {
          slidesPerView: 1,
        },
        // when window width is >= 480px
        480: {
          slidesPerView: 2,
        },
        768: {
          slidesPerView: 4,
        },
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
        disabledClass: 'my-button-disabled',
      },
      pagination: {
        el: '.swiper-pagination',
        type: 'bullets',
      },
    };

    this.init();
  }

  /**
   * Mthode d'initialisation
   */
  init() {
    let options = this.defaultOptions;

    // Gestion des paramtres diffrents lorsqu'on veut avoir
    // 2 slides visibles sur grand cran et une seule sur petit cran
    if (this.element.dataset.carousel == 'split') {
      options = {
        ...this.defaultOptions,
        ...{
          breakpoints: {
            768: {
              slidesPerView: 2,
            },
          },
        },
      };
    }

    if (this.element.dataset.carousel == 'scroll') {
      options = {
        ...this.defaultOptions,
        ...{
          scrollbar: {
            el: '.swiper-scrollbar',
            draggable: true,
            scrollbarHide: false
          },
          navigation: false,
          pagination: false,
        },
      };
    }

    // Instanciation d'un nouveau Swiper avec les options
    new Swiper(this.element, options);
  }
}
